import { Component, Node } from "untrue";

import { RouterHistory } from "@untrue/web";

import Helper from "../../../helpers/Helper";

class Topic extends Component {
  constructor(props) {
    super(props);

    const { topic, name } = props;

    this.state = { open: topic !== null && topic.name === name };

    this.on("update", this.handleUpdateOpen);
  }

  handleUpdateOpen = () => {
    const { topic, name } = this.props;

    const { open } = this.state;

    const { open: prevOpen } = this.prevState;

    if (open !== prevOpen) {
      return;
    }

    this.updateState({
      open: open || (topic !== null && topic.name === name),
    });
  };

  onToggle = () => {
    const { open } = this.state;

    this.updateState({ open: !open });
  };

  render() {
    const {
      route: {
        params: { page: routePage },
      },
      category,
      name,
      pages,
    } = this.props;

    const { open } = this.state;

    return new Node("div", { class: "sidebar-category" }, [
      new Node(
        "div",
        { class: "sidebar-category-name", onclick: this.onToggle },
        [
          new Node("div", { class: "sidebar-category-name-span" }, name),
          new Node("i", {
            class: open ? "fas fa-chevron-down" : "fas fa-chevron-right",
          }),
        ]
      ),
      new Node(
        "ul",
        {
          class: `sidebar-category-list ${
            open ? "sidebar-category-list-open" : ""
          }`,
        },
        pages.map(
          (page) =>
            new Node(
              "li",
              { class: "sidebar-category-list-item" },
              new Node(
                "a",
                {
                  class: `sidebar-category-list-item-link ${
                    page.slug === routePage
                      ? "sidebar-category-list-item-link-active"
                      : ""
                  }`,
                  href: Helper.getPath(`/${category.slug}/${page.slug}`),
                  onclick: RouterHistory.onClick,
                },
                page.name
              )
            )
        )
      ),
    ]);
  }
}

export default Topic;
