import { Node } from "untrue";

import { Head, RouterHistory } from "@untrue/web";

import Helper from "../../helpers/Helper";

import capisciConfig from "../../../../capisci.config";

function Home() {
  return [
    new Node(
      Head,
      new Node("title", `${capisciConfig.title} - ${capisciConfig.slogan}`)
    ),
    new Node("div", { class: "home" }, [
      new Node(
        "div",
        { class: "home-section home-section-even" },
        new Node("div", { class: "home-container home-hero" }, [
          new Node("h1", { class: "home-hero-title" }, capisciConfig.title),
          new Node(
            "p",
            { class: "home-hero-description" },
            capisciConfig.description
          ),
          new Node(
            "a",
            {
              class: "home-hero-link",
              href: Helper.getPath(
                `/${capisciConfig.content[0].categories[0].slug}/${capisciConfig.content[0].categories[0].topics[0].pages[0].slug}`
              ),
              onclick: RouterHistory.onClick,
            },
            "Get started"
          ),
        ])
      ),
      new Node("div", { class: "home-section" }, [
        new Node("div", { class: "home-container" }, [
          new Node("h2", { class: "home-title" }, "Some title"),
          new Node(
            "p",
            { class: "home-paragraph" },
            "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat."
          ),
        ]),
      ]),
      new Node("div", { class: "home-section home-section-even" }, [
        new Node("div", { class: "home-container" }, [
          new Node("h2", { class: "home-title" }, "Some title"),
          new Node(
            "p",
            { class: "home-paragraph" },
            "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat."
          ),
        ]),
      ]),
    ]),
  ];
}

export default Home;
