import { Node } from "untrue";

import Topic from "./Topic";

import capisciConfig from "../../../../../capisci.config";

function Sidebar({ route }) {
  const {
    params: { category: routeCategory = null, page: routePage = null },
  } = route;

  const category =
    routeCategory !== null
      ? capisciConfig.content[0].categories.find(
          (tmpCategory) => tmpCategory.slug === routeCategory
        )
      : null;

  let topic = null;

  if (category !== null && routePage !== null) {
    for (const tmpTopic of category.topics) {
      for (const page of tmpTopic.pages) {
        if (page.slug === routePage) {
          topic = tmpTopic;
        }
      }
    }
  }

  return new Node(
    "aside",
    { class: "sidebar" },
    new Node(
      "div",
      { class: "sidebar-widget" },
      new Node(
        "div",
        { class: "sidebar-widget-content" },
        new Node(
          "div",
          { class: "sidebar-widget-inner" },
          category.topics.map(
            (tmpTopic) =>
              new Node(Topic, {
                route,
                category,
                topic,
                ...tmpTopic,
              })
          )
        )
      )
    )
  );
}

export default Sidebar;
