import { Component, Node, Ref } from "untrue";

import { RouterHistory } from "@untrue/web";

import Helper from "../../helpers/Helper";

import capisciConfig from "../../../../capisci.config";

class Header extends Component {
  constructor(props) {
    super(props);

    this.state = { scrolled: false };

    this.headerRef = new Ref();

    this.on("mount", this.handleMountScroll);
    this.on("unmount", this.handleUnmountScroll);
  }

  handleMountScroll = () => {
    window.document.addEventListener("scroll", this.scrollListener);
  };

  handleUnmountScroll = () => {
    window.document.removeEventListener("scroll", this.scrollListener);
  };

  scrollListener = () => {
    const header = this.headerRef.getValue();

    if (header === null) {
      return;
    }

    const scrolled = window.scrollY > header.offsetHeight;

    this.updateState({ scrolled });
  };

  render() {
    const { route: { params: { category: routeCategory = null } = {} } = {} } =
      this.props;

    const { scrolled } = this.state;

    return new Node(
      "header",
      {
        ref: this.headerRef,
        class: `header ${scrolled ? "header-scrolled" : ""}`,
      },
      new Node("div", { class: "header-content" }, [
        new Node(
          "h3",
          { class: "header-title" },
          new Node(
            "a",
            { href: Helper.getPath("/"), onclick: RouterHistory.onClick },
            capisciConfig.title
          )
        ),
        new Node(
          "ul",
          { class: "header-list" },
          capisciConfig.content[0].categories.map(
            (tmpCategory) =>
              new Node(
                "li",
                { class: "header-list-item" },
                new Node(
                  "a",
                  {
                    class: `header-list-item-link ${
                      routeCategory !== null &&
                      tmpCategory.slug === routeCategory
                        ? "header-list-item-link-active"
                        : ""
                    }`,
                    href: Helper.getPath(
                      `/${tmpCategory.slug}/${tmpCategory.topics[0].pages[0].slug}`
                    ),
                    onclick: RouterHistory.onClick,
                  },
                  tmpCategory.name
                )
              )
          )
        ),
      ])
    );
  }
}

export default Header;
