import { Node } from "untrue";

function Loading() {
  return new Node("div", { class: "page page-loading" }, [
    new Node("div", { class: "page-loading-content" }, [
      new Node("div", { class: "page-loading-title" }),
      [0, 1, 2, 3, 4].map(() => [
        new Node("div", { class: "page-loading-paragraph" }, [
          new Node("div", { class: "page-loading-line" }),
          new Node("div", { class: "page-loading-line" }),
          new Node("div", { class: "page-loading-line" }),
        ]),
        new Node("div", { class: "page-loading-pre" }),
      ]),
    ]),
    new Node(
      "div",
      { class: "page-loading-sidebar" },
      [0, 1].map(() => [
        new Node("div", { class: "page-loading-sidebar-title" }),
        new Node("div", { class: "page-loading-sidebar-subtitle" }),
        new Node("div", { class: "page-loading-sidebar-subtitle" }),
        new Node("div", { class: "page-loading-sidebar-subtitle" }),
      ])
    ),
  ]);
}

export default Loading;
