import { Component, Node, Ref, Wrapper } from "untrue";

import Prism from "prismjs";

import { Head, RouterHistory } from "@untrue/web";

import Helper from "../../helpers/Helper";

import PageContext from "../../context/PageContext";

import Loading from "./Loading";
import Error from "./Error";

import capisciConfig from "../../../../capisci.config";

class Page extends Component {
  constructor(props) {
    super(props);

    this.state = { parsed: false, headers: [] };

    this.articleRef = new Ref();
    this.asideRef = new Ref();

    this.on("mount", this.handleMountFetch);

    this.on("render", this.handleRenderArticle);
  }

  handleMountFetch = () => {
    const { loading, done } = this.props;

    if (!loading && !done) {
      this.onFetch();
    }
  };

  onFetch = () => {
    const {
      route: {
        params: { category, page },
      },
      onFetch,
    } = this.props;

    onFetch(category, page);
  };

  handleRenderArticle = () => {
    const { data } = this.props;

    if (data === null) {
      return;
    }

    const article = this.articleRef.getValue();

    if (article === null) {
      return;
    }

    article.innerHTML = data;

    Prism.highlightAll();

    this.fillHeaders();

    const { hash } = window.location;

    window.location.hash = "";
    window.location.hash = hash;
  };

  fillHeaders() {
    const article = this.articleRef.getValue();

    const headers = [];

    const elements = article.querySelectorAll("h2,h3");

    let i = 0;

    while (i < elements.length) {
      const element = elements[i];

      const header = {
        id: element.getAttribute("id"),
        title: element.textContent,
        subheaders: [],
      };

      i++;

      while (i < elements.length) {
        const subelement = elements[i];

        if (subelement.nodeName.toLowerCase() !== "h3") {
          break;
        }

        header.subheaders.push({
          id: subelement.getAttribute("id"),
          title: subelement.textContent,
        });

        i++;
      }

      headers.push(header);
    }

    this.updateState({ parsed: true, headers });
  }

  render() {
    const {
      route: {
        params: { category: routeCategory, page: routePage },
      },
      loading,
      done,
      error,
    } = this.props;

    const { parsed, headers } = this.state;

    const category = capisciConfig.content[0].categories.find(
      (tmpCategory) => tmpCategory.slug === routeCategory
    );

    let currentPage = null;
    let previousPage = null;
    let nextPage = null;

    let tmpPage = null;

    for (const topic of category.topics) {
      for (const page of topic.pages) {
        if (currentPage !== null && nextPage === null) {
          nextPage = page;
        }

        if (page.slug === routePage) {
          currentPage = page;

          previousPage = tmpPage;
        }

        tmpPage = page;
      }
    }

    return [
      new Node(
        Head,
        new Node("title", `${currentPage.name} | ${capisciConfig.title}`)
      ),
      loading ? new Node(Loading) : null,
      error !== null ? new Node(Error, { onFetch: this.onFetch }) : null,
      done
        ? new Node("div", { class: `page ${!parsed ? "page-hidden" : ""}` }, [
            new Node("div", { class: "page-content" }, [
              new Node("article", {
                ref: this.articleRef,
                class: "page-markdown",
              }),
              previousPage !== null || nextPage !== null
                ? new Node("div", { class: "page-more" }, [
                    new Node(
                      "div",
                      { class: "page-more-button page-more-prev" },
                      previousPage !== null
                        ? new Node(
                            "a",
                            {
                              class: "page-more-link",
                              href: Helper.getPath(
                                `/${category.slug}/${previousPage.slug}`
                              ),
                              onclick: RouterHistory.onClick,
                            },
                            [
                              new Node(
                                "span",
                                {
                                  class:
                                    "page-more-icon page-more-icon-to-left",
                                },
                                new Node("i", { class: "fas fa-chevron-left" })
                              ),
                              new Node("div", { class: "page-more-text" }, [
                                new Node(
                                  "span",
                                  { class: "page-more-label" },
                                  "Previous"
                                ),
                                new Node(
                                  "span",
                                  { class: "page-more-name" },
                                  previousPage.name
                                ),
                              ]),
                            ]
                          )
                        : null
                    ),
                    new Node(
                      "div",
                      { class: "page-more-button page-more-next" },
                      nextPage !== null
                        ? new Node(
                            "a",
                            {
                              class: "page-more-link",
                              href: Helper.getPath(
                                `/${category.slug}/${nextPage.slug}`
                              ),
                              onclick: RouterHistory.onClick,
                            },
                            [
                              new Node("div", { class: "page-more-text" }, [
                                new Node(
                                  "span",
                                  { class: "page-more-label" },
                                  "Next"
                                ),
                                new Node(
                                  "span",
                                  { class: "page-more-name" },
                                  nextPage.name
                                ),
                              ]),
                              new Node(
                                "span",
                                {
                                  class:
                                    "page-more-icon page-more-icon-to-right",
                                },
                                new Node("i", { class: "fas fa-chevron-right" })
                              ),
                            ]
                          )
                        : null
                    ),
                  ])
                : null,
            ]),
            headers.length > 0
              ? new Node(
                  "aside",
                  { ref: this.asideRef, class: "page-sidebar" },
                  new Node(
                    "div",
                    { class: "page-sidebar-widget" },
                    new Node(
                      "div",
                      { class: "page-sidebar-widget-content" },
                      new Node(
                        "div",
                        { class: "page-sidebar-widget-inner" },
                        new Node(
                          "ul",
                          { class: "page-sidebar-list" },
                          headers.map(
                            ({ id, title, subheaders }) =>
                              new Node(
                                "li",
                                { class: "page-sidebar-list-item" },
                                new Node(
                                  "div",
                                  { class: "page-sidebar-list-item-container" },
                                  [
                                    new Node("a", { href: `#${id}` }, title),
                                    subheaders.length > 0
                                      ? new Node(
                                          "ul",
                                          { class: "page-sidebar-sublist" },
                                          subheaders.map(
                                            ({ id, title }) =>
                                              new Node(
                                                "li",
                                                {
                                                  class:
                                                    "page-sidebar-sublist-item",
                                                },
                                                new Node(
                                                  "a",
                                                  { href: `#${id}` },
                                                  title
                                                )
                                              )
                                          )
                                        )
                                      : null,
                                  ]
                                )
                              )
                          )
                        )
                      )
                    )
                  )
                )
              : null,
          ])
        : null,
    ];
  }
}

export default Wrapper.wrapContext(Page, PageContext, (props) => {
  const {
    route: {
      params: { category, page },
    },
  } = props;

  const map = PageContext.getMap();

  const key = PageContext.getKey(category, page);

  const {
    loading = false,
    done = false,
    error = null,
    data = null,
  } = map?.[key] ?? {};

  const { onFetch } = PageContext;

  return { loading, done, error, data, onFetch };
});
