import { Node } from "untrue";

import Header from "./Header";
import Footer from "./Footer";

import Sidebar from "./Sidebar/Sidebar";

import NotFound from "../../screens/NotFound/NotFound";

import capisciConfig from "../../../../capisci.config";

function Template({ route, children }) {
  const {
    params: { category: routeCategory = null, page: routePage = null },
    path,
  } = route;

  let page = null;

  if (routeCategory !== null) {
    const category = capisciConfig.content[0].categories.find(
      (tmpCategory) => tmpCategory.slug === routeCategory
    );

    if (category !== undefined) {
      for (const topic of category.topics) {
        for (const tmpPage of topic.pages) {
          if (tmpPage.slug === routePage) {
            page = tmpPage;
          }
        }
      }
    }
  }

  return [
    new Node("div", { class: "wrapper" }, [
      new Node(Header, { route: page !== null ? route : {} }),
      path === "/"
        ? children
        : page !== null
        ? new Node("div", { class: "content" }, [
            new Node(Sidebar, { route }),
            children,
          ])
        : new Node(NotFound, { route }),
    ]),
    new Node(Footer),
  ];
}

export default Template;
