import { Node } from "untrue";

import { Head } from "@untrue/web";

import capisciConfig from "../../../../capisci.config";

function NotFound() {
  return [
    new Node(Head, new Node("title", `Not found | ${capisciConfig.title}`)),
    new Node("div", { class: "not-found" }, [
      new Node("span", { class: "not-found-number" }, 404),
      new Node("span", { class: "not-found-text" }, "Not found."),
    ]),
  ];
}

export default NotFound;
