import { Context } from "untrue";

import { marked } from "marked";

import Helper from "../helpers/Helper";

class PageContext extends Context {
  constructor(store) {
    super(store);

    this.map = {};
  }

  getMap() {
    return this.map;
  }

  onFetch = async (category, page) => {
    const key = this.getKey(category, page);

    const item = { loading: true, done: false, data: null, error: null };

    this.map[key] = item;

    this.update();

    try {
      const path = Helper.getPath(`/md/v0/${category}/${page}.md`);

      const response = await fetch(path);

      const text = await response.text();

      const parsed = marked.parse(text);

      item.done = true;
      item.data = parsed;
    } catch (error) {
      item.error = error;
    } finally {
      item.loading = false;

      this.update();
    }
  };

  getKey(category, page) {
    return `${category}-${page}`;
  }
}

export default new PageContext();
