import { Component, Node, Ref } from "untrue";

import capisciConfig from "../../../../capisci.config";

class Footer extends Component {
  constructor(props) {
    super(props);

    this.spanRef = new Ref();

    this.on("mount", this.handleMountYear);
  }

  handleMountYear = () => {
    const span = this.spanRef.getValue();

    const date = new Date();

    span.textContent = date.getFullYear();
  };

  render() {
    return new Node("footer", { class: "footer" }, [
      new Node("div", { class: "footer-content" }, [
        new Node("div", [
          "Copyright  ",
          new Node("span", { ref: this.spanRef }),
          ", ",
          new Node(
            "a",
            { class: "footer-link", href: capisciConfig.author.url },
            capisciConfig.author.name
          ),
          ".",
        ]),
        new Node(
          "div",
          capisciConfig.social.length > 0
            ? new Node(
                "ul",
                { class: "footer-social" },
                capisciConfig.social.map(
                  ({ name, icon, url }) =>
                    new Node(
                      "a",
                      {
                        class: "footer-icon",
                        href: url,
                        title: name,
                        target: "_blank",
                      },
                      new Node("i", { class: `fa-brands ${icon}` })
                    )
                )
              )
            : null
        ),
      ]),
    ]);
  }
}

export default Footer;
