import { Node } from "untrue";

function Error({ onFetch }) {
  return new Node(
    "div",
    { class: "page page-error" },
    new Node("div", { class: "page-error-content" }, [
      new Node(
        "span",
        { class: "page-error-message" },
        "An error occurred while loading this page."
      ),
      new Node(
        "button",
        { class: "page-error-button", onclick: onFetch },
        "Retry"
      ),
    ])
  );
}

export default Error;
